<template>
    <div id="home">
        <head-top></head-top>
        <top-nav></top-nav>
        <div class="main-container">
            <project-list :isHome="true" @itemOnClick="itemOnClick" @proData="proList"></project-list>
            <el-button type="primary" plain @click="showProPos" class="all-location" :class="showAll?'all-location-show': 'all-location-not'">总览</el-button>
            <div class="nav-right view row-container">
                <div class="page-title">
                    <div v-if="showAll">
                        <span>所有项目位置</span>
                    </div>
                    <div v-else>
                        <span v-if="project.location">{{project.name + '所在地'}}</span>
                        <span v-else>项目所在地</span>
                        <span v-if="project.location">{{'：' + project.location}}</span>
                    </div>
                </div>
                <div id="container">
                    <div id="map-container">
                    </div>
                </div>
            </div>
        </div>
    </div>

</template>
<script>
    import headTop from '../../Header.vue'
    import footerBottom from '../../Footer.vue'
    import topNav from '../../TopNav.vue'
    import projectList from './ProjectList.vue'
    import AMap from 'AMap'

    export default {
        data() {
            return {
                map: {},
                markers: [],
                staticMarkers: [],
                geocoder: '',
                currentId: '',
                project: {},
                projectList: [],
                showAll: false,
            }
        },
        components: {
            headTop,
            footerBottom,
            topNav,
            projectList,
        },
        mounted() {
            var self = this;
            self.init();
        },
        methods: {
            init: function () {
                var self = this;
                //创建map，默认中心:南京
                self.map = new AMap.Map('container', {
                    resizeEnable: true,
                    dragEnable: true,
                    keyboardEnable: true,
                    doubleClickZoom: true,
                    zoom: 8,
                    center: [118.7964700000, 32.0583800000],
                });
                //map小插件
                AMap.plugin(['AMap.ToolBar', 'AMap.Scale', 'AMap.OverView'],
                    function () {
                        self.map.addControl(new AMap.ToolBar());
                        self.map.addControl(new AMap.Scale());
                        self.map.addControl(new AMap.OverView({
                            isOpen: true
                        }));
                    });
                //map城市插件
                AMap.plugin('AMap.Geocoder', function () {

                    self.geocoder = new AMap.Geocoder({});
                });
                self.map.setFitView();
            },
            itemOnClick(item) {
                var self = this;
                //防止重复显示
                if (item.id === self.currentId) {
                    return;
                }
                self.showAll = false;
                self.project = item;
                self.map.remove(self.staticMarkers);
                self.map.remove(self.markers);
                var cityName = item.location;
                self.geocoder.getLocation(cityName, function (status, result) {
                    if (status == 'complete' && result.geocodes.length) {
                        var ProjectLocation = [result.geocodes[0].location.getLng(), result.geocodes[0].location.getLat()];
                        var marker = new AMap.Marker({
                            position: ProjectLocation,
                            map: self.map
                        });
                        self.map.setZoomAndCenter(9.1, ProjectLocation);
                        marker.setAnimation('AMAP_ANIMATION_BOUNCE');
                        self.markers.push(marker);
                    }
                    else {
                        self.map.remove(self.markers);
                    }
                    //根据城市名获取id，定位和添加弹跳标志
                    self.currentId = item.id;
                });
            },
            proList(...data) {
                this.projectList = data;
            },
            showProPos() {
                let self = this;
                self.showAll = true;
                self.map.remove(self.markers);
                self.map.remove(self.staticMarkers);
                let data = self.projectList[0];
                for (let i = 0;i<data.length;i++) {
                    self.geocoder.getLocation(data[i].position, function (status, result) {
                        if (status == 'complete' && result.geocodes.length) {
                            var ProjectLocation = [result.geocodes[0].location.getLng(), result.geocodes[0].location.getLat()];
                            // var ProjectLocation = [result.geocodes[0].location.getLng()+self.randomObj[i][0], result.geocodes[0].location.getLat()+self.randomObj[i][1]];
                            var marker = new AMap.Marker({
                                position: ProjectLocation,
                                map: self.map,
                                title:data[i].project_name
                            });
                            self.map.setZoomAndCenter(7.4, [119.6850585938, 32.6578757370]);
                            self.staticMarkers.push(marker);
                        }
                    });
                }
                self.currentId = '';
            }
        }
    }
</script>
<style>
    #container {
        margin-left: 300px;
        margin-top: 120px;
        width: calc(100% - 300px);
        height: calc(100% - 120px);
        overflow: hidden;
    }

    .pro-head {
        position: absolute;
        z-index: 300;
        margin-top: -10px;
    }

    .all-location {
        position: absolute;
        left: 183px;
        top: 114px;
    }

    #home .all-location-show {
        background: #409EFF;
        border-color: #409EFF;
        color: #fff;
    }
    .all-location-not.el-button--primary.is-plain:focus{
        color: #409EFF;
        background: #ecf5ff;
        border-color: #b3d8ff;
    }
</style>



